import apiObject from '@/dataserve/student_serve.js'
import _ from 'lodash'

const state = {
  lessonPlan:''
};
const getters = {
  getLessonPlan:(state)=>{
    return state.lessonPlan
  }
};
const mutations = {

SET_LESSONPLAN:(state,dt)=>{
  state.lessonplan=dt;
},
};
const actions = {

add_lessonplan:({commit},dt)=>{
  const url1='/LpIds/'
  apiObject.post(url1,dt)
    .then(rr=>{
      commit('SET_LESSONPLAN',rr.data)
    })
    .catch(error=>{
      console.log('****',error);
    })
},
load_lessonplan:({commit},dt)=>{
    const ob={
      where:dt
    }
    const url1 = '/LpIds?filter='+JSON.stringify(ob);
    console.log(url1);
    return new Promise((resolve,reject)=>{
      apiObject.get(url1).then(response=>{
        commit('SET_LESSONPLAN',response.data)
        resolve(response.data[0])
      }).catch(error=>{
      console.log(error);
      reject(error)
      })
    });
},
load_lessonplan_topic:({commit},unitId)=>{
  const url1 = '/LpUnits/'+unitId+'/lpTopics';
  console.log(url1);
  return new Promise((resolve,reject)=>{
    apiObject.get(url1).then(response=>{
      resolve(response.data)
  }).catch(error=>{
    console.log(error);
    reject(error)
  })
})
},
load_lessonplan_unit:({commit},lpId)=>{
  const url1 = '/LpIds/'+lpId+'/lpUnits';
  console.log(url1);
  return new Promise((resolve,reject)=>{
    apiObject.get(url1).then(response=>{
      resolve(response.data)
  }).catch(error=>{
    console.log(error);
    reject(error)
  })
});
},
add_lp_topic:({commit},dt)=>{
  const url1='/LpTopics/'
  dt.topicId=0
  return new Promise(function(resolve, reject) {
      apiObject.post(url1,dt)
        .then(rr=>{
          resolve(rr.data)
        })
        .catch(error=>{
          reject(error)
          console.log('****',error);
        })
    });
   },
add_lessonplan_unit:({commit},dt)=>{
  const url1='/LpUnits/'
  dt.unitId=0
  return new Promise(function(resolve, reject) {
      apiObject.post(url1,dt)
        .then(rr=>{
          resolve(rr.data)
        })
        .catch(error=>{
          reject(error)
          console.log('****',error);
        })
    });
   },
   remove_lp_topic:({commit},id)=>{
      const url1='/LpTopics/'+id
      return new Promise(function(resolve, reject) {
        apiObject.delete(url1)
          .then(rr=>{
            resolve(rr.data)
          })
          .catch(error=>{
            reject(error)
            console.log('****',error);
          })
        });
  },
   remove_lessonplan_unit:({commit},id)=>{
      const url1='/LpUnits/'+id

      return new Promise(function(resolve, reject) {
        apiObject.delete(url1)
          .then(rr=>{
            resolve(rr.data)
          })
          .catch(error=>{
            reject(error)
            console.log('****',error);
          })
        });
    }
}
export default {
  namespaced:true,
  state,
  getters,
  mutations,
  actions
}



